import React from 'react'
import Constants, { userToken } from "src/Constants";
import {
    CRow,
    CCol,
    CWidgetStatsF,
} from '@coreui/react'

// import {CIcon ,cilChartPie} from '@coreui/icons-react';
import CIcon from '@coreui/icons-react';
import { cilLightbulb } from '@coreui/icons';
import { useUserStore } from 'src/userStore';

const Trafic = () => {

    const hasPerm = useUserStore((state) => state.hasPerm);

    const [count, setCount] = React.useState(0)

    React.useEffect(() => {
        let url = Constants.traficReport;


        (async () => {
            let response = await fetch(url, {
                method: 'GET',
                headers: {
                    'Authorization': userToken(),

                }
            })
            let data = await response.json();
            setCount(data)


        })()
    }, [])

    return (
        <CRow>
            <CCol xs={3} className={`flex-grow-1 ${!hasPerm('view_crossing') ? 'd-none' : ''}`}>
                <CWidgetStatsF
                    className="mb-3"
                    color="success"
                    title="Last 24 Hours"
                    icon={<CIcon icon={cilLightbulb} height={36} />}
                    value={count.day}
                />
            </CCol>
            <CCol xs={3} className={`flex-grow-1 ${!hasPerm('view_camera') ? 'd-none' : ''}`}>
                <CWidgetStatsF
                    className="mb-3"
                    color="primary"
                    title="Last 7 days"
                    icon={<CIcon icon={cilLightbulb} height={36} />}
                    value={count.week}
                />
            </CCol>
            <CCol xs={3} className={`flex-grow-1 ${!hasPerm('view_closure') ? 'd-none' : ''}`}>
                <CWidgetStatsF
                    className="mb-3"
                    color="danger"
                    title="Last 1 Month"
                    icon={<CIcon icon={cilLightbulb} height={36} />}
                    value={count.month}
                />
            </CCol>
            <CCol xs={3} className={`flex-grow-1 ${!hasPerm('view_closure') ? 'd-none' : ''}`}>
                <CWidgetStatsF
                    className="mb-3"
                    color="info"
                    title="Total Traffic"
                    icon={<CIcon icon={cilLightbulb} height={36} />}
                    value={count.all}
                />
            </CCol>
        </CRow>
    )
}

export default Trafic
